.nav__contents{
    display:flex;
    justify-content: space-between;
}
.nav__logo{
    position:relative;
    object-fit: contain;
}
.nav__logo{
    width:140px;
}
.nav__logo:hover{
cursor: pointer;
}

nav{
    position: absolute;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all .5s;
}
.nav__black{
    color:black;
}